.intro-video {
  position: relative;
  min-height: 342px;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    content: '';
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.59);
    z-index: 1;
  }

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    z-index: 2;
  }

  &__text {
    width: 50%;
    z-index: 3;
  }

  &__playblock {
    width: 50%;
    z-index: 3;
  }

  &__bg-img {
    img {
      top: -80px;
      margin: 0 auto;
    }
  }
}
